import { $next } from "@jx3box/jx3box-common/js/https";
import { __Root } from "@jx3box/jx3box-common/data/jx3box.json";
import dateFormat from "../utils/dateFormat";
import xfmap from "@jx3box/jx3box-data/data/xf/xf.json";

function syncRedis(data, vm) {
    let redisData = transferForRedis(data);
    console.log("æ­£åœ¨æ‰§è¡ŒredisåŒæ­¥ä½œä¸š:", redisData);
    return $next().post("/api/macro/publish", redisData);
}

function transferForRedis(data) {
    let author = data.author;
    let pid = data.ID;

    let _ = {
        author: author,
        user_id: ~~data.post_author || 0,
        post_id: pid,
        post_status: data.post_status,
        data: {},
        lang: data.post_meta.lang,
        original: data.original,
    };

    let xf = xfmap[data.post_subtype]["id"] + "";

    data.post_meta.data.forEach((item, i) => {
        // æè¿°å¤„ç†
        let desc = "";

        // å¥‡ç©´
        desc += "ã€å¥‡ç©´ã€‘";
        let talent = "";
        try {
            talent = item.talent && JSON.parse(item.talent);
            desc += talent.sq;
        } catch (e) {
            console.log("å¥‡ç©´ç¼–ç è§£æå¤±è´¥", e);
        }

        // æ—¶é—´
        desc += "\nã€æœ€åæ›´æ–°äºã€‘";
        desc += dateFormat(new Date());

        // æ¥æº
        desc += "\nã€æ¥æºã€‘JX3BOX";

        if (!item.name) item.name = Date.now();

        _.data[item.name] = {
            author: author,
            key: item.name,
            version: Date.now(),
            icon: ~~item.icon || 13,
            xf: xf || 0,

            data: item.macro,
            desc: desc,

            about: __Root + "macro/" + pid,
        };
    });

    return _;
}

export { syncRedis };
